<template>
    <input 
        type="text" 
        class="app-form"
        @input="$emit('update:modelValue',$event.target.value)"
    >
</template>

<script>
export default {
    name: 'AppInput',
    props: ['modelValue'],
    emits: ['update:modelValue'],
}
</script>

<style lang="scss">
.app-form{
    display: block;
    width: 100%;
    padding: 10px 5px;
    font-size: 1rem;
    border-bottom: 1px solid teal;
    margin: 0px 0px 20px 0px;
    transition: all .4s ease 0s;

    &:focus{
        box-shadow: 0px 4px 6px -4px teal;
    }
}
</style>
