<template>
    <li class="products__card card-products _anim-items">
        <div class="card-products__wrp">
            <div class="card-products__body">
                <div class="card-products__img">
                    <img :src="product.img" :alt="product.name">
                </div>
                <div class="card-products__desc">
                    <div class="card-products__name">{{ product.name }}
                        
                    </div>
                    <div class="card-products__price">{{ product.price }}</div>
                </div>
                <div class="card-products__buttons">
                    <button 
                        class="card-products__button _icon-12" 
                        @click="zoomImage(product.id)"
                    >
                    </button>
                    <button 
                        class="card-products__button _icon-14" 
                        @click="likeItem"
                    ></button>
                    <button 
                        class="card-products__button _icon-15" 
                        @click="addToCart(product)" 
                    ></button>
                </div>
                <div class="card-products__add-info">
                    <div 
                        class="card-products__add-info-block card-products__add-info-block_green" 
                        v-if="product.discount"
                    >
                        {{ product.discount }}
                    </div>
                    <div 
                        class="card-products__add-info-block card-products__add-info-block_blue"
                        v-if="product.new"
                    >
                        New
                    </div>
                    <div 
                        class="card-products__out"
                        v-if="product.out"
                    >
                        Out of stoke
                    </div>
                </div>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    props: {
        product: Object,
    },
    data(){
        return {
            isClassActive: false,
        }
    },
    methods: {
        zoomImage(id){
            this.$emit('zoom',id);
        },
        likeItem(event){
            if(localStorage.getItem('canUserUse')){
                event.target.classList.toggle('liked');
            }
            else{
                this.$emit('openModelWindow');
            }
        },
        addToCart(product){
            if(localStorage.getItem('canUserUse')){
                this.$emit('addToCart', product);
            }
            else{
                this.$emit('openModelWindow');
            }
        }
    }
}
</script>

<style lang="scss">

.card-products {
    position: relative;
    padding: 0px 15px;
    margin: 0px 0px 30px 0px;

    &__wrp{
        flex: 0 1 21.875rem;
        max-width: 21.875rem;
    }

    &__body {
        cursor: pointer;
        overflow: hidden;
        background: #F1F6F9;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.2);
        border-radius: 3px;
        padding: 40px 50px;
        transition: all .4s ease 0s;
        @media all and (min-width:62em) {
            &:hover{
                box-shadow: 0px 0px 20px #71B3C6;
            }
        }
       
        @media all and (max-width:30em) {
            padding: 25px;
        }
    }

    &__img {
        width: 15.625rem;
        height: 9.375rem;
        margin: 0px 0px 10px 0px;
        img{
            max-width: 100%;
        }
    }

    &__buttons {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__button {
        padding: 8px;
        display: flex;
        align-items: center;
        justify-content: start;
        background: #71B3C6;
        border-radius: 2px;
        margin: 0px 16px 0px 0px;
        color: #fff;
        transition: all .4s ease 0s;

        &:last-child{
            margin-right: 0;
        }

        @media all and (min-width:62em) {
            &:hover{
                color: #d9d7d7;
                background: #4b7d8b;
            }
        }
        @media all and (max-width:62em) {
            padding: 12px;
            font-size: 1rem;
            transition: unset;
        }

    }

    &__add-info {
        position: absolute;
        right: 30px;
        top: 30px;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    &__add-info-block {
        width: 38px;
        height: 38px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 0.75rem;
        line-height: 172.68%;
        letter-spacing: 0.045em;
        color: #FFFFFF;

        &_green {
            background: #264A51;
            margin: 0px 0px 14px 0px;
        }

        &_blue {
            background: #71B3C6;
            margin: 0px 0px 30px 0px;
        }
    }

    &__out {
        position: relative;
        transform: translateX(15px);
        width: 85px;
        height: 30px;
        background: #264A51;
        font-weight: 500;
        font-size: 0.75rem;
        line-height: 172.68%;
        display: flex;
        justify-content: center;
        align-items: center;
        letter-spacing: 0.045em;
        color: #FFFFFF;
    }

    &__desc {
        padding: 15px 0px;
    }

    &__name,
    &__price {
        font-size: 0.9375rem;
        font-weight: 600;
        line-height: 133.5%;
        letter-spacing: 0.045em;
        color: #264A51;
    }
    &__stars {
    }
}

.liked{
    background: red;
}

</style>