<template>
    <main class="main">
        <MainInroduction/>
        <MainPartners/>
        <MainProducts 
            :products="products"
            @change-tab="chagneTab"
            @zoom="zoomImage"
            @open-model-window="openModelWindow"
            @add-to-cart="addToCart"
        />
        <Transition name="popup">
            <AppPopup v-if="signUpPopupOpen" @click="closePopup">
                <h2 class="popup__title _icon-08"></h2>
                <template v-if="accountNotCreated">
                    <AppInput tabindex="1"
                        placeholder="create login ..." 
                        v-model="localUserAccount.login"
                        @focus="isDataValid=false"
                    ></AppInput>
                    <AppInput tabindex="2"
                        placeholder="create password ..."
                        v-model="localUserAccount.password"
                        @focus="isDataValid=false"
                    ></AppInput>
                </template>
                <template v-else>
                    <p class="successfully">
                        Account successfully created
                    </p>
                </template>
                <Transition name="not-valid">
                    <p v-if="isDataValid" class="not-valid">Invalid data</p>
                </Transition>
                <div class="loader-block" v-if="isLoaderVisible">
                    <div class="loader"></div>
                </div>
                <AppButton @click.prevent="createAccount" v-if="accountNotCreated" tabindex="3">Create Account</AppButton>
            </AppPopup>
        </Transition>
        <Transition name="popup">
            <AppPopup v-if="logInPopupOpen" @click="closePopup">
                <h2 class="popup__title _icon-08"></h2>
                <template v-if='isUserNotLogedIn'>
                    <AppInput tabindex="1"
                        placeholder="enter login ..." 
                        v-model="localLogInUserAccount.login"
                        @focus="dataExist=false"
                    ></AppInput>
                    <AppInput tabindex="2"
                        type="password"
                        placeholder="enter password ..."
                        v-model="localLogInUserAccount.password"
                        @focus="dataExist=false"
                    ></AppInput>
                    <Transition name="not-valid">
                        <p v-if="dataExist" class="not-valid">Invalid login or password</p>
                    </Transition>
                    <AppButton @click.prevent="logInAccount" tabindex="3">Log in</AppButton>
                </template>
                <template v-else>
                    <p class="successfully">
                        you are logged in
                    </p>
                </template>
                <div class="loader-block" v-if="isLogInLoaderVisible">
                    <div class="loader"></div>
                </div>
            </AppPopup>
        </Transition>
        <Transition name="popup">
            <AppPopup v-if="isZoomImgVisible" @click="closePopup">
                <div class="popup__img">
                    <img :src="zoomImgSrc" alt="Image">
                </div>
            </AppPopup>
        </Transition>
        <Transition name="popup">
            <AppPopup class="cart" v-if="isCartOpen"  @click="closePopup">
                <p v-if="cartArr.length===0" class="empty">Oppps... cart is empty</p>
                <ul class="cart__list" v-else>
                    <TransitionGroup name="cart-list">
                    <li class="cart__item item-cart" v-for="item in cartArr" :key="item.id">
                        <div class="item-cart__body">
                            <div class="item-cart__img">
                                <img :src="item.img" :alt="item.name">
                            </div>
                            <div class="item-cart__desc">
                                <div class="item-cart__name">{{ item.name }}</div>
                                <div class="item-cart__price">{{ item.price }}</div>
                                <button class="item-cart__button item-cart__button_green">Buy</button>
                                <button class="item-cart__button item-cart__button_red" @click="removeFromCart(item.id)">Remove</button>
                            </div>
                        </div>
                    </li>
                </TransitionGroup>
                </ul>
            </AppPopup>
        </Transition>
        <MainDiscount/>
        <MainDemonstration/>
        <MainFeedback/>
        <MainContacts/>
    </main>
</template>

<script>
import MainInroduction from './MainComponents/MainInroduction.vue';
import MainPartners from './MainComponents/MainPartners.vue';
import MainProducts from './MainComponents/MainProducts.vue';
import MainDiscount from './MainComponents/MainDiscount.vue';
import MainFeedback from './MainComponents/MainFeedback.vue';
import MainContacts from './MainComponents/MainContacts.vue';
import MainDemonstration from './MainComponents/MainDemonstration.vue';

export default {
    components: {
        MainInroduction,
        MainProducts,
        MainPartners,
        MainDiscount,
        MainFeedback,
        MainContacts,
        MainDemonstration,
    },
    props: {
        signUpPopupOpen: Boolean,
        logInPopupOpen: Boolean,
        userAccount: Object,
        logInUserAccount: Object,
        products: Object,
        isAccountCreated: Boolean,
        zoomImgSrc: String,
        isZoomImgVisible: Boolean,
        isCartOpen: Boolean,
        cartArr: Array,
    },
    data(){
        return {
            localUserAccount: this.userAccount,
            localLogInUserAccount: this.logInUserAccount,
            isDataValid: false,
            dataExist: false,
            isLoaderVisible: false,
            isLogInLoaderVisible: false,
            accountNotCreated: true,
            localIsAccountCreated: this.isAccountCreated,
            isUserNotLogedIn: true,
        }
    },
    methods: {
        closePopup(event){
            this.$emit('closePopup', event);
        },
        createAccount(){
            if(this.localUserAccount.login && this.localUserAccount.password){
                this.isLoaderVisible = true;
                setTimeout(()=>{
                    this.isLoaderVisible = false;
                    this.accountNotCreated = false;
                    this.$emit('createAccount', this.localUserAccount,this.localIsAccountCreated);
                },2000)
            }
            else{
                this.isDataValid = true;
            }
        },
        logInAccount(){
            this.isLogInLoaderVisible = true;
            setTimeout(()=>{
                let isTrueData = 
                this.localLogInUserAccount.login == localStorage.getItem('login') &&
                this.localLogInUserAccount.password == localStorage.getItem('password');
                if(isTrueData){
                    this.$emit('logInComplete');
                    this.isUserNotLogedIn = false;
                }
                else{
                    this.dataExist = true;
                }
                this.isLogInLoaderVisible = false;
            },1000)
        },
        chagneTab(param){
            this.$emit('changeTab', param);
        },
        zoomImage(id){
            this.$emit('zoom',id);
        },
        openModelWindow(){
            this.$emit('openModelWindow');
        },
        addToCart(product){
            this.$emit('addToCart',product);
        },
        removeFromCart(id){
            this.$emit('removeFromCart',id);
        }
    },
    mounted(){
        if(localStorage.getItem('canUserUse')){
            this.isUserNotLogedIn = false;
        }
    }
}
</script>

<style lang="scss">
.main{
    flex: 1 1 auto;
}

.popup-enter-active,
.popup-enter-leave{
    transition: all .5s ease 0s;
}

.popup-enter-from,
.popup-leave-to {
    opacity: 0;
}

.not-valid-enter-active,
.not-valid-enter-leave{
    transition: all .5s ease 0s;
}

.not-valid-enter-from,
.not-valid-leave-to {
    opacity: 0;
}

.not-valid {
    text-align: left;
    padding: 5px 0px 10px;
    margin-top: -10px;
    color: red;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 0.75rem;
}

.loader-block{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(225, 222, 222, 0.215);
    display: flex;
    align-items: center;
    justify-content: center;
}

.loader {
  border: 5px solid #f3f3f3; 
  border-top: 5px solid #3498db; 
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.successfully{
    font-size: 1.5rem;
    text-transform: uppercase;
    line-height: 140%;
    position: relative;
    padding: 40px 0px;
}

div.popup__body.cart{
    width: 51.25rem;
    height: 26.25rem;
}

div.popup__body.cart .popup__content{
    width: 100%;
}

.item-cart {
    padding: 10px 5px;
    border-bottom: 1px solid gray;
    margin: 0px 0px 20px 0px;
    &__body {
        display: flex;
        @media all and (max-width:36.125em) {
            align-items: center;
            justify-content: center;
            flex-direction: column;
        }
    }

    &__img {
        width: 200px;
        height: 150px;
        overflow: hidden;
        img{
            max-width: 100%;
        }
    }

    &__desc {
        text-align: left;
    }

    &__name {
        font-size: 1.5rem;
        font-weight: 700;
        margin: 0px 0px 10px 0px;
        @media all and (max-width:36.125em) {
            text-align: center;
        }
    }

    &__price {
        font-weight: 600;
        margin: 0px 0px 20px 0px;
        @media all and (max-width:36.125em) {
            text-align: center;
        }
    }

    &__button {
        width: 100px;
        padding: 5px 10px;
        border-radius: 5px;
        font-weight: 700;
        font-size: 1rem;
        background: none;
        transition: all .4s ease 0s;
        &_red{
            color: red;
            border: 1px solid red;
            margin: 0px 0px 0px 10px;
            @media all and (min-width:62em) {
                &:hover{
                    background: red;
                    color: #fff;
                }
            }
           
        }
        &_green{
            color: #002A32;
            border: 1px solid #002A32;
            @media all and (min-width:62em) { 
                &:hover{
                    background: #002A32;
                    color: #fff;
                }
            }
           
        }
        @media all and (max-width:23.625em) {
            display: block;
            margin: 0px 0px 20px 0px;
            transform: translateX(30%);
        }
    }
}

.cart-list-move, 
.cart-list-enter-active,
.cart-list-leave-active {
  transition: all 0.5s ease;
}

.cart-list-enter-from,
.cart-list-leave-to {
  opacity: 0;
  transform: translateX(30px);
}

.cart-list-leave-active {
  position: absolute;
}

.empty{
    font-family: 'Dosis';
    position: absolute;
    padding: 20px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    font-size: 2rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    text-align: center;
    line-height: 120%;
    width: 100%;
}

</style>