<template>
  <div class="wrapper" ref="wrapper">
    <AppHeader 
      :is-burger-open="isBurgerOpen"
      :items-in-cart="itemsInCart"
      :is-sign-up-btn-not-active="isSignUpBtnNotActive"
      @open-burger="openBurger"
      @open-sign-up-popup="openSignUpPopup"
      @open-log-in-popup="openLogInPopup"
      @open-cart="openCart"
    />
    <AppMain 
      :sign-up-popup-open="isSignUpPopupOpen"
      :log-in-popup-open="isLogInPopupOpen"
      :user-account="userAccount"
      :log-in-user-account = 'enteredAccountInfo'
      :products="filteredProducts"
      :zoom-img-src="zoomImageSrc"
      :is-zoom-img-visible="isZoomVisible"
      :is-cart-open="isCartOpen"
      :cart-arr="cartArr"
      @closePopup="closePopup"
      @create-account="createAccount"
      @log-in-complete="logInComplete"
      @change-tab="changeTab"
      @zoom="zoomImage"
      @open-model-window="openModelWindow"
      @add-to-cart="addToCart"
      @remove-from-cart="removeFromCart"
    />
    <AppFooter />
      <AppModalWindow
        :is-visible="userCantUse"
      >
        <p>Please login to your account</p>
      </AppModalWindow>
  </div>
</template>

<script>
import AppHeader from './components/AppHeader.vue';
import AppMain from './components/AppMain.vue';
import AppFooter from './components/AppFooter.vue';

export default {
  name: 'App',
  components: {
      AppHeader,
      AppMain,
      AppFooter
  },
  data(){
    return {
        isBurgerOpen: false,
        isSignUpPopupOpen: false,
        isLogInPopupOpen: false,
        filterParam: 0,
        zoomImageSrc: '',
        isZoomVisible: false,
        isCartOpen: false,
        userCantUse: false,
        isClassAdded: true,
        isSignUpBtnNotActive: false,
        userAccount: {
          login: '',
          password: '',
        },
        enteredAccountInfo: {
          login: '',
          password: '',
        },
        products: [
            {id:0, price: '423$', name:'Single sofa #1', img: 'img/Products/01.png', new:true, discount: '-20%', out: true},
            {id:1, price: '423$', name:'Single sofa #2', img: 'img/Products/02.png', new:true, discount: '', out: false},
            {id:2, price: '423$', name:'Single sofa #3', img: 'img/Products/03.png', new:true, discount: '-20%', out: false},
            {id:3, price: '423$', name:'Single sofa #4', img: 'img/Products/04.png', new:true, discount: '-20%', out: true},
            {id:4, price: '423$', name:'Single sofa #5', img: 'img/Products/05.png', new:true, discount: '-20%', out: false},
            {id:5, price: '423$', name:'Single sofa #6', img: 'img/Products/06.png', new:true, discount: '-20%', out: false},
        ],
        cartArr: [],
        itemsInCart: 0,
     }
  },
  methods: {
    openBurger(value){
      this.isBurgerOpen = value;
    },
    openSignUpPopup(){
        if(!localStorage.getItem('login')){
          this.isSignUpPopupOpen = true;
        }
    },
    openLogInPopup(){
      if(this.isBurgerOpen){
        this.isBurgerOpen = false;
      }
      this.isLogInPopupOpen = true;
    },
    closePopup(event){
        if(event.target.classList.contains('popup__wrp')){
          this.isSignUpPopupOpen = false;
          this.isLogInPopupOpen = false;
          this.isZoomVisible = false;
          this.isCartOpen = false;
        }
    },
    createAccount(usersInfo){
        const login = usersInfo.login;
        const password = usersInfo.password;
        localStorage.setItem('login',login);
        localStorage.setItem('password',password);
        this.isSignUpBtnNotActive = true;
    },
    logInComplete(){
      localStorage.setItem('canUserUse','true');
    },
    changeTab(param){
      this.filterParam = param;
    },
    openModelWindow(){
      this.userCantUse = true;
        setTimeout(()=>{
          this.userCantUse = false;
        },2500)
    },
    zoomImage(id){
      if(localStorage.getItem('canUserUse')){
        this.zoomImageSrc = this.products[id].img;
        this.isZoomVisible = true;
      }
      else{
        this.openModelWindow();
      }
    },
    openCart(){
      this.isCartOpen = true;
    },
    addToCart(product){
      if(!this.cartArr.includes(product)){
        this.cartArr.push(product);
        this.itemsInCart++;
        this.isAddedToCart = true;
      }
    } ,
    removeFromCart(id){
      this.cartArr = this.cartArr.filter(item=>item.id !== id);
      this.itemsInCart--;
    }
  }, 
   computed: {
    filteredProducts(){
      return this.products.slice(this.filterParam);
    }
  }, 
  created(){
    if(localStorage.getItem('login')){
      this.isSignUpBtnNotActive = true;
    }
  },
  mounted(){
      /* eslint-disable */
      window.addEventListener('keyup',(event)=>{
        if(event.key==='Escape'){
          this.isSignUpPopupOpen = false;
          this.isLogInPopupOpen = false;
          this.isZoomVisible = false;
        }
      });

      const animItems = document.querySelectorAll("._anim-items");
      if (animItems.length > 0) {
          window.addEventListener("scroll", animOnScroll);
          function animOnScroll() {
              for (let index = 0; index < animItems.length; index++) {
                  const animItem = animItems[index];
                  const animItemHeight = animItem.offsetHeight;
                  const animItemOffset = offset(animItem).top;
                  const animStart = 4;

                  let animItemPoint = window.innerHeight - animItemHeight / animStart;
                  if (animItemHeight > window.innerHeight) {
                      animItemPoint - window.innerHeight - window.innerHeight / animStart;
                  }
                  if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                      animItem.classList.add("_active");
                  }
                  else {
                      if (!animItem.classList.contains('_anim-repeat')) {
                          animItem.classList.remove("_active");
                      }
                  }
              }
          }
          function offset(el) {
              const rect = el.getBoundingClientRect(),
                  scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
                  scrollTop = window.pageYOffset || document.documentElement.scrollTop;
              return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
          }
          animOnScroll();
      }
      function iOS() {
        return [
          'iPad Simulator',
          'iPhone Simulator',
          'iPod Simulator',
          'iPad',
          'iPhone',
          'iPod'
        ].includes(navigator.platform) || (navigator.userAgent.includes("Mac") && "ontouchend" in document)
      }
      if(Boolean(iOS())){
        document.body.classList.add('ios');
      }
  }
}
</script>

<style lang="scss"></style>