<template>
    <div class="products">
        <div class="products__container">
            <h2 class="products__title _anim-items"><span>Our Products</span></h2>
            <ul class="products__nav _anim-items">
                <li class="products__item">
                    <button class="products__button active" @click="chagneTab(0,$event)">Single Sofa</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(1,$event)">Living Room</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(2,$event)">Kitchen</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(3,$event)">Bed Room</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(4,$event)">Bath Room</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(5,$event)">Decorations</button>
                </li>
                <li class="products__item">
                    <button class="products__button" @click="chagneTab(4,$event)">Ceramics</button>
                </li>
            </ul>
            <ul class="products__cards _anim-items">
                <TransitionGroup name="products">
                    <ProductItem 
                        v-for="product in products" 
                        :key="product.id" 
                        :product="product"
                        @zoom="zoomImage"
                        @open-model-window="openModelWindow"
                        @add-to-cart="addToCart"
                    />
                </TransitionGroup>
            </ul>
        </div>
    </div>
</template>

<script>
import ProductItem from './ProductsComponents/ProductItem.vue';
export default {
    components: {
        ProductItem,
    },
    props: {
        products: Object,
    },
    methods: {
        chagneTab(param,event){
            this.$emit('changeTab', param);
            document.querySelectorAll('.products__button').forEach(el=>el.classList.remove('active'));
            event.target.classList.add('active');
        },
        zoomImage(id){
            this.$emit('zoom',id)
        },
        openModelWindow(){
            this.$emit('openModelWindow');
        },
        addToCart(product){
            this.$emit('addToCart',product);
        }
    }
}
</script>

<style lang="scss">
.products {
    padding: 94px 0px 113px;
    background: #F3F3F3;

    &__container {
        text-align: center;
    }

    &__title {
        font-family: 'Dosis';
        font-weight: 600;
        font-size: 2.875rem;
        line-height: 133.5%;
        letter-spacing: 0.045em;
        color: #264A51;
        margin: 0px 0px 27px 0px;
        overflow: hidden;
        span{
            display: block;
            transition: all .7s ease 0s;
            transform: translateY(60px);
        }
        &._active{
            span{
                transform: translateY(0px);
            }
        }
    }

    &__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        column-gap: 10px;
        row-gap: 10px;
        margin: 0px 0px 28px 0px;
        opacity: 0;
        transition: all 1s ease 0s;
        &._active{
            opacity: 1;
        }
        @media all and (max-width:48em) {
            justify-content: center;
        }
    }

    &__button {
        padding: 6px;
        font-family: 'Open Sans';
        font-size: 1.0625rem;
        line-height: 135.3%;
        letter-spacing: 0.045em;
        color: #264A51;
        background: none;
        transition: color .4s ease 0s;
        
        &.active{
            border: 2px solid #3E5860;
            border-radius: 2px;
        }
        @media all and (min-width:62em) {
            &:hover{
                color: #2f8b9d;
            }
        }
    }

    &__cards{
        margin: 0px -15px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        opacity: 0.6;
        transition: all 1s ease 0s;
        &._active{
            opacity: 1;
        }
    }
}

.products-move, 
.products-enter-active,
.products-leave-active {
  transition: all 0.5s ease;
}

.products-enter-from,
.products-leave-to {
  opacity: 0;
  transform: translateX(30px);
}

.products-leave-active {
  position: absolute;
}

.button-active{

}
</style>