<template>
    <header class="header _anim-items" :class="{'header-scrolled': hasHeaderBackground}">
        <div class="header__container">
            <div class="header__content" :class="{'burger-menu-active': localIsBurgerOpen}">
                <div class="header__authorization">
                    <button class="header__button _icon-01" @click="logIn">LOGIN</button>
                    <button class="header__button _icon-02" @click="signUp" :class="{'sign-up-active': isSignUpBtnNotActive}">SING UP</button>
                </div>
                <div class="header__buttons">
                    <a href="https://www.google.com/intl/en/gmail/about/" class="header__button _icon-03">MAILING LIST</a>
                    <button class="header__button _icon-04" @click="copyLink">SHARE</button>
                    <button class="header__button _icon-05" @click="openCart"><span v-if="itemsInCart">{{ itemsInCart }}</span>CART</button>
                </div>
            </div>
            <div 
                class="header__burger" 
                @click="toggleMenuBurger" 
                :class="{'burger-btn-active': localIsBurgerOpen}"
            >
                <div></div>
                <div></div>
                <div></div>
            </div>
        </div>
        <Transition name="copy">
            <div class="successfully-copy" v-if="copy">Link copied to clipboard</div>
        </Transition>
    </header>
</template>

<script>
export default {
    props: {
        isBurgerOpen: Boolean,
        itemsInCart: Number,
        isSignUpBtnNotActive: Boolean,
    },
    data(){
        return {
            hasHeaderBackground: false,
            localIsBurgerOpen: this.isBurgerOpen,
            copy: false,
        }
    },
    mounted(){
        window.addEventListener('scroll',this.toggleHeaderBackground)
    },
    methods: {
        toggleMenuBurger(){
            this.localIsBurgerOpen = !this.localIsBurgerOpen;
            document.body.classList.toggle('lock');
            this.$emit('openBurger', this.localIsBurgerOpen);
        },
        toggleHeaderBackground(){
            const y = pageYOffset;
            if(y>40){
                this.hasHeaderBackground = true;
            }else{
                this.hasHeaderBackground = false;
            }
        },
        signUp(){
            if(!localStorage.getItem('login')){
                this.localIsBurgerOpen = false;
                document.body.classList.remove('lock');
                this.$emit('openSignUpPopup');
            }
        },
        logIn(){
            this.localIsBurgerOpen = false;
            document.body.classList.remove('lock');
            this.$emit('openLogInPopup');
        },
        copyLink(){
            var link = location.href;
            navigator.clipboard.writeText(link)
            .then(() => {
               this.copy = true;
               setTimeout(()=>{
                this.copy = false;
               },3000)
            })
            .catch(err => {
                console.error('Error in copying text: ', err);
            });
        },
        openCart(){
            this.localIsBurgerOpen = false;
            document.body.classList.remove('lock');
            this.$emit('openCart');
        }
    }
}
</script>

<style lang="scss">

.header {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    width: 100%;
    z-index: 50;
    transition: all .4s ease 0s;
    opacity: 0;

    &._active{
        opacity: 1;
    }

    &__container {
        min-height: 5.0625rem;
        padding: 28px 15px;
        display: flex;
        justify-content: flex-end;
        @media all and (max-width:48em) {
            min-height: 3rem;
            padding: 15px;
        }
    }

    &__content{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        @media all and (max-width:48em) {
            display: block;
            position: fixed;
            z-index: 50;
            left: -100%;
            top: 0;
            width: 100%;
            height: 100%;
            transition: all .4s ease 0s;
            background: rgb(0, 42, 50, 0.95);
            overflow: auto;
            padding: 80px 30px;
        }
    }

    &__authorization{
        margin: 0px 15px 0px 0px;
    }

    &__button {
        position: relative;
        font-size: 1rem;
        line-height: 162%;
        letter-spacing: -0.02em;
        color: #3E5860;
        background: transparent;
        margin: 0px 35px 0px 0px;
        transition: all .4s ease 0s;
        @media all and (min-width:62em) {
            &:hover,
            &:hover::before{
                color: #3d91aa;
            }
        }
       

        &::before{
            color: #fff;
            margin: 0px 12px 0px 0px;
            transition: all .4s ease 0s;
        }

        @media all and (max-width:48em) {
            display: block;
            margin: 0px 0px 20px 0px;
            font-size: 2rem;
            color: #fff;
            text-align: left;
        }

        span{
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            left: 10px;
            top: 10px;
            width: 15px;
            height: 15px;
            background: red;
            border-radius: 50%;
            color: #fff;
            font-size: 0.6rem;
            font-weight: 700;
        }

    }

    &__burger{
        display: none;
        @media all and (max-width:48em) {
            position: relative;
            z-index: 100;
            display: block;
            width: 35px;
            height: 30px;
            div{
                position: relative;
                width: 100%;
                height: 1px;
                background: #fff;
                transition: all .4s ease 0s;
                margin: 0px 0px 9px 0px;
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}

.burger-btn-active{
    div{
        &:nth-child(1){
            transform-origin: center right;
            top: -3px;
            transform: rotate(-45deg);
        }
        &:nth-child(2){
            transform: scale(0);
        }
        &:nth-child(3){
            transform-origin: center right;
            bottom: -2px;
            transform: rotate(45deg);
        }
    }
}

.burger-menu-active{
    left: 0;
}

.header-scrolled {
    background: #002A32;
    .header__button{
        color: #fff;
        @media all and (min-width:62em) {
            &:hover{
                color: #c5c1c1;
            }
        }
    }
}

.successfully-copy{
    padding: 15px;
    background: #3C6C7F;
    border-radius: 5px;
    color: #fff;
    max-width: 16rem;
    position: fixed;
    right: 20px;
    bottom: 10%;
}

.copy-enter-active,
.copy-leave-active {
  transition: all 0.5s ease 0s;
}

.copy-enter-from,
.copy-leave-to {
    opacity: 0;
}

.sign-up-active{
    text-decoration: line-through;
    color: #c8c5c5;
    &::before{
        color: #c8c5c5;
    }
    @media all and (min-width:62em) {
        &:hover{
        color: #c8c5c5;
        }
        &:hover::before{
            color: #c8c5c5;
        }
    }
}

</style>